<template>

  <el-tree
    :data="skills"
    :props="defaultProps"
  >
  </el-tree>
</template>

<script>
  import _ from "lodash"
  export default {
    props:['evaluation'],
    methods: {
      traverse(obj) {
        console.log(this.evaluation);
        console.log(obj);
        let result = [];
        _.forOwn(obj, (val, key) => {
          if (_.isArray(val)) {
            val.forEach(el => {
              traverse(el);
            });
          } else if (_.isObject(val)) {
            traverse(val);
          } else {
            if (key === 'discriminatorId' && val === 1) result.push(obj);
          }
        });
      },
    },
    computed:{
      skills(){
        var skill = this.traverse(this.evaluation);

        console.log(skill);
        return skill
      }
    },
    data(){
      return {
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    }


  }
</script>



